.categoriesList{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

@media (max-width:1520px) {
    .categoriesList{
        display: -ms-grid;
        display: grid;
        -ms-grid-columns: (1fr)[6];
        grid-template-columns: repeat(6,1fr);
        grid-row-gap: 24px;
    }
}