.header{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.sorts{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.sorts > :not(:last-child){
    margin-right: 16px;
}

.sort1{
    width: 136px;
}
.sort2{
    width: 112px;
}
.sort3{
    width: 152px;
}

.sortsTabs{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

@media (max-width:1500px) {
    .header {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
    }
    .sorts{
        margin-bottom: 24px;
    }
    .sortsTabs{
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
    }
    .sortsTabs > :first-child{
        margin-bottom: 24px;
    }
}

@media (max-width:992px){
    .sortsTabs > :first-child{
        margin-bottom: 0;
    }
    .sortsTabs > :last-child{
        display: none;
    }
}