.categories{
    padding-top: 8px;
    max-width: 1492px;
    margin: 16px auto 292px;
}

.body{
    margin-top: 35px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.filter{
    width: 355px;
    padding-right: 51px;
    margin-right: 24px;
}

.products{
    background: #FFFFFF;

    -webkit-box-flex: 1;

        -ms-flex-positive: 1;

            flex-grow: 1;
    padding: 16px;
    -webkit-box-shadow: 0px 1px 2px rgba(58, 58, 68, 0.24), 0px 2px 4px rgba(90, 91, 106, 0.24);
            box-shadow: 0px 1px 2px rgba(58, 58, 68, 0.24), 0px 2px 4px rgba(90, 91, 106, 0.24);
    border-radius: 8px;
}

@media (max-width:1500px) {
    .categories{
        max-width: 1200px;
        padding-left: 10px;
        padding-right: 10px;
    }
    .filter{
        padding-right: 21px;
    }
}