.userMenu{
    position: absolute;
    top: 100%;
    right: 0;

    background: white;

    width: 300px;
    height: 500px;
    border: 2px solid #2264D1;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    z-index: 1;
}
.userMenu.active{
    opacity: 1;
    visibility: visible;
}

.userWrapper{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    height: 100%;
    padding: 20px;

}

.userName{
    font-family: var(--QuickSand-Bold);
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.0075em;
    color: black;
}
.userLogout{
    font-family: var(--QuickSand-Bold);
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.0075em;
    color: #2264D1;
}