.table{
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 16px 1fr 16px 1fr 16px 1fr;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 16px;
    grid-row-gap: 16px;
}
.table.solo{
    -ms-grid-columns: 1fr;
    grid-template-columns: repeat(1, 1fr);
    grid-column-gap: 0px;
}
.table.solo .item{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.item{
    padding: 16px 16px 12px 16px;
}

.load{
    position: relative;

    background: #ccc;

    overflow: hidden;
}
.load::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    
    background: #ddd;
    
    width: 0;
    height: 100%;
    -webkit-box-shadow: 0px 0px 37px 10px #ddd;
            box-shadow: 0px 0px 37px 10px #ddd;
    -webkit-transform: translateX(-6em) skewX(-30deg);
        -ms-transform: translateX(-6em) skewX(-30deg);
            transform: translateX(-6em) skewX(-30deg);
    -webkit-animation: light 3s cubic-bezier(0.645, 0.045, 0.355, 1) infinite;
            animation: light 3s cubic-bezier(0.645, 0.045, 0.355, 1) infinite;
}

@-webkit-keyframes light {
    0% {
        background: #ddd;
        -webkit-transform: translateX(-6em) skewX(-30deg);
                transform: translateX(-6em) skewX(-30deg)
    }
    50% {
        background: #aaa;
    }
    100% {
        background: #ddd;
        -webkit-transform: translateX(20em) skewX(-30deg);
                transform: translateX(20em) skewX(-30deg);
    }
}

@keyframes light {
    0% {
        background: #ddd;
        -webkit-transform: translateX(-6em) skewX(-30deg);
                transform: translateX(-6em) skewX(-30deg)
    }
    50% {
        background: #aaa;
    }
    100% {
        background: #ddd;
        -webkit-transform: translateX(20em) skewX(-30deg);
                transform: translateX(20em) skewX(-30deg);
    }
}


.itemImgWrapper{
    width: 224px;
    height: 224px;
    margin-bottom: 12px;
}
.table.solo .itemImgWrapper{
    min-height: 224px;
    min-width: 224px;
    margin: 0 12px 0 0;
}
.itemImgWrapper img{
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}
.table.solo .itemText{
    max-width: 250px;
    width: 100%;
}
.itemText{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.itemTitle{
    height: 74px;
    font-family: var(--Inter-Regular);
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.0275em;
    color: #19191D;
    overflow: hidden;
}
.itemPrice{
    position: relative;

    height: 36px;
    margin: 8px 0;
    font-family: var(--Inter-Bold);
    font-size: 24px;
    line-height: 150%;
    color: rgba(0, 0, 0, 0.87);
    overflow: hidden;
}
.itemPrice .offer{
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);

    display: -webkit-box;

    display: -ms-flexbox;

    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    width: 70px;
    margin: 6px 0;
    padding: 4px 0;
    border-radius: 4px;
    font-family: var(--Roboto-Regular);
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.4px;
}
.itemPrice .coolDeal{
    background: #FDEDF2;

    color: #C23564;
}
.itemPrice .discount{
    background: #ECF7ED;

    color: #37833B;
    text-transform: uppercase;
}
.itemSubtitle{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 43px;
    margin-bottom: 9px;
    font-family: var(--Inter-Regular);
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.018em;
    color: #787885;
    overflow: hidden;
}
.itemFooter{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.table.solo .itemFooter{
    margin-left: auto;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.table.solo .itemRating{
    margin-bottom: 16px;
}
.itemRating{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   width: 117px;
}
.itemRateTitle{
    font-family: var(--Roboto-Medium);
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.1px;
    color: #5E6366;
}


@media (max-width:1500px){
    .table{
        -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width:1220px){
    .table{
        -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (max-width:992px){
    .table{
        -ms-grid-columns: (1fr)[1];
        grid-template-columns: repeat(1, 1fr);
    }
}