.departaments{
    background: #FFFFFF;

    display: -webkit-inline-box;

    display: -ms-inline-flexbox;

    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border: 1px solid #9DC2FF;
    border-radius: 8px;
}
.departaments svg{
    margin: 12px 8px 12px;
}
.departaments span{
    display: inline-block;
    width: 232px;
    margin-right: 20px;
    font-family: var(--QuickSand-Bold);
    font-size: 24px;
    line-height: 150%;
    text-align: center;
    color: #2264D1;
}


.tree {
    margin-top: 40px;
}

.filters{
    margin-top: 32px;
}
