ul.tree{
    padding:0;
}
.tree ul{
    padding-left: 8px;
    max-height: 100vh;
    opacity: 1;
    visibility: visible;
    -webkit-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear;
}
.tree ul:global(.list-hide){
    max-height: 0px;
    opacity: 0;
    visibility: hidden;
}

.tree li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}

.tree span{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    font-family: var(--Inter-Medium);
    color: #19191D;
}

.tree li > span:not(:global(.last-child)){
    position: relative;

    padding: 6px 8px 10px 48px;
}
.tree li > span:global(.last-child){
    padding: 6px 8px 10px 40px;
    font-family: var(--Inter-Regular);
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.0275em;
}

.tree li > span::before{
    content: '';
    position: absolute;
    left: 16px;
    top: 50%;
    
    width: 24px;
    height: 24px;
    -webkit-transform: translateY(-50%) rotate(-90deg);
        -ms-transform: translateY(-50%) rotate(-90deg);
            transform: translateY(-50%) rotate(-90deg);
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}
.tree li > span:global(.show)::before{
    background-image: url('../../../images/chevron.svg');
    -webkit-transform: translateY(-50%) rotate(0deg);
        -ms-transform: translateY(-50%) rotate(0deg);
            transform: translateY(-50%) rotate(0deg);
}
.tree li > span:global(.hide)::before{
    background-image: url('../../../images/chevron.svg') ;
}

.tree li > span > :first-of-type{
    font-family: var(--Inter-Medium);
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.0275em;
    color: #19191D;
}
.tree li > span > :last-of-type{
    font-family: var(--Inter-Regular);
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.018em;
    color: #787885;
}
.tree li > span:global(.span-parent){
    padding-top: 6px;
    padding-bottom: 5px;
}


.button{
    background: rgba(235, 242, 255, 1);

    height: 40px;

    font-family: var(--Roboto-Bold);
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.75px;
    text-transform: capitalize;
    color: #2264D1;
}
.seeMore{
    position: relative;

    margin: 0 0 6px 40px;
    padding: 0 48px 0 12px;

}
.seeMore::after{
    content: '';
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    right: 12px;

    background: url('../../../images/chevron_more.svg');

    width: 28px;
    height: 28px;
}
.expand{
    margin-left: 48px;
    padding: 0 12px;
}