.header{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    height: 80px;
    padding-right: 16px;
}
.search{
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    width: 40%;
}
.search input{
    background: #EDEDF0;

    width: 100%;
    padding: 8px 44px;
    border-radius: 99px;
    font-family: var(--Inter-Regular);
    font-size: 16px;
    line-height: 158%;    
    color: #19191D;
}

.search input::-webkit-input-placeholder{
    color: #787885;
}

.search input::-moz-placeholder{
    color: #787885;
}

.search input:-ms-input-placeholder{
    color: #787885;
}

.search input::-ms-input-placeholder{
    color: #787885;
}

.search input::placeholder{
    color: #787885;
}
.search .iconSearch{
    position: absolute;
    left: 15px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);

    width: 17.5px;
}
.search .iconCross{
    position: absolute;
    right: 17px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);

    width: 14px;
}

.user{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.userCart{
    height: 30px;
    margin-right: 35px;
}
.userCart img{
    height: 100%;
}

.user{
    position: relative;
}

.userMenuActive .userLogo{
    border-radius: 0px;
    border-bottom-color: transparent;
}
.userLogo{
    background: white;
    
    display: -webkit-box;
    
    display: -ms-flexbox;
    
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 50px;
    width: 50px;
    border: 2px solid #2264D1;
    border-radius: 50%;
    -webkit-transition: border-radius .3s ease-in-out, border-bottom .0s ease-in-out .1s;
    -o-transition: border-radius .3s ease-in-out, border-bottom .0s ease-in-out .1s;
    transition: border-radius .3s ease-in-out, border-bottom .0s ease-in-out .1s;
}
.userLogo img{
    height: 66%;
}
.userButtons{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.userButtons button{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 40px;
    padding: 0 12px;
    border: 1px solid #9DC2FF;
    border-radius: 4px;
    font-family: var(--QuickSand-Bold);
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.0075em;
    color: #2264D1;
}
.userButtons .userSignIn{
    background: #2264D1;

    margin-left: 10px;
    color: #fff;
}
