.related{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.relatedTitle{
    margin: 4px 8px 0 0;
    font-family: var(--Inter-Medium);
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.018em;
    color: #19191D;
}
.relatedClips{
    background: #EDEDF0;

    display: -webkit-box;

    display: -ms-flexbox;

    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 32px;
    margin-top: 8px;
    padding: 0 12px;
    border-radius: 99px;
    font-family: var(--Inter-Regular);
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.018em;
    color: #4A4B57;
}
.relatedClips:not(:last-child){
    margin-right: 8px;
}